<template>
  <ParagraphWithCarousel
    title="Final UI Design"
    paragraph="Based on the user testing, the important change was to add a bottom navigation bar (study sets, starred flashcards, achievements) at the bottom rather than having a home screen. This eliminates redundant content and lets users see their study sets when they first open the app."
    :carouselStyle="containerStyle"
    :carouselItems="items"
    :imgStyle="imgStyle"
  />
</template>

<script>
import ParagraphWithCarousel from '../common/ParagraphWithCarousel';

import gocard_finalui1_cover from "../../assets/img/gocard_finalui1_cover.jpg";
import gocards_finaldesign2 from "../../assets/img/gocards_finaldesign2.jpg";
import gocards_finaldesign3 from "../../assets/img/gocards_finaldesign3.jpg";
import gocards_finaldesign4 from "../../assets/img/gocards_finaldesign4.jpg";
import gocards_finaldesign5 from "../../assets/img/gocards_finaldesign5.jpg";
import gocards_finaldesign6 from "../../assets/img/gocards_finaldesign6.jpg";

export default {
  components: {
    ParagraphWithCarousel,
  },
  data() {
    return {
      items: [
        {img: gocard_finalui1_cover, alt: "Final Design mock"},
        {img: gocards_finaldesign2, alt: "Final Design mock"},
        {img: gocards_finaldesign3, alt: "Final Design mock"},
        {img: gocards_finaldesign4, alt: "Final Design mock"},
        {img: gocards_finaldesign5, alt: "Final Design mock"},
        {img: gocards_finaldesign6, alt: "Final Design mock"},
      ],
      containerStyle: {
        height: "100%"
      },
      imgStyle: {
        padding: "24px",
        width: "80%",
        margin: "0 auto",
      },
    };
  }

};
</script>

<style lang="scss" scoped>

</style>



