<template>
  <div>
    <Width100 v-bind:addPadding="true">
      <Width50>
        <Paragraph
          subtitle="The Problem"
          paragraph="It can be difficult to keep track of all the jargon when studying a new discipline. Students need a way to methodically categorize, reference, and study new vocabulary and concepts. How can I design a mobile app that empowers people to learn new vocabulary?"
        />
        <Paragraph
          subtitle="The Solution"
          paragraph="By creating an app that uses visuals, audio, rewards, and encouraging reminders will help students move forward confidently in their field."
        />
        <Paragraph
          subtitle="Tools"
          paragraph="Marvelapp (Prototype), Sketch (Wireframes and Visual Design)"
        />
      </Width50>
      <Width50>
        <div class="marvel-container">
          <iframe src="https://marvelapp.com/1g277496?emb=1&iosapp=false&frameless=false" title="Prototype"  width="100%" height="784" allowTransparency="true" frameborder="0"></iframe>
        </div>
      </Width50>
    </Width100>
    <LineBreak />
  </div>
</template>

<script>
import LineBreak from '../common/LineBreak';
import Paragraph from '../common/Paragraph';
import Width50 from '../common/layout/Width50';
import Width100 from '../common/layout/Width100';

export default {
  components: {
    LineBreak,
    Paragraph,
    Width50,
    Width100,
  },

};
</script>

<style lang="scss" scoped>
.marvel-container {
  margin: 0 auto;
  text-align: center;
}
</style>



