    paragraph="Based on my user interviews, I have cumulated all common personality traits and compiled the data and mix them together to create Whitney: A young tech-savvy student and professional."
    alt="Persona"
  />
</template>

<script>
import ParagraphWithImage from '../common/ParagraphWithImage';

import gocards_persona from "../../assets/img/gocards_persona.jpg";

export default {
  components: {
    ParagraphWithImage,
  },
  data() {
    return {
      imgSrc: gocards_persona,
      title: "Persona",
    };
  }

};
</script>

<style lang="scss" scoped>
</style>



