<template>
  <ParagraphWithImage
    :imgSrc="imgSrc"
    title="User Journey"
    subtitle="Shaping Ideas"
    paragraph="Keeping Whitney in mind, I mapped her user behavioral flow from opening the app to reviewing her study sets. Understanding her decision making and flow within her study experience allowed me to brainstorm the neccessary feature and oportunities required for Whitney to successully navigate through the app."
    alt="User Journey"
  />
</template>

<script>
import ParagraphWithImage from '../common/ParagraphWithImage';

import gocard_journeymap from "../../assets/img/gocard_journeymap.jpg";

export default {
  components: {
    ParagraphWithImage,
  },
  data() {
    return {
      imgSrc: gocard_journeymap,
      title: "Concepting",
    };
  }

};
</script>

<style lang="scss" scoped>
</style>



