<template>
  <div>
    <Width100>
      <Paragraph
        v-bind:center="true"
        title="Key Learnings"
        paragraph="It is important to put yourself in the user's shoes and understand the needs and problems in order to create successful products. Usability Testing not only helps fix errors, but also creates new solutions to meet the user's needs."
      />
    </Width100>
    <Width100 v-bind:addPadding="true">
      <OrderedList
        title="Next Steps"
        titleColor="#6200EE"
        v-bind:items="nextSteps"
      />
    </Width100>
  </div>
</template>

<script>
import LineBreak from '../common/LineBreak';
import OrderedList from '../common/OrderedList';
import Paragraph from '../common/Paragraph';
import Width100 from '../common/layout/Width100';

export default {
  components: {
    LineBreak,
    OrderedList,
    Paragraph,
    Width100,
  },
  data() {
    return {
      nextSteps: [
        "Development of Gocard extension for browsers and integration in other mobile content browsing apps.",
        "Add animation to help the enhance the study process.",
        "Conduct A/B testing with the design of the home screen.",
      ],
    };
  }

};
</script>

<style lang="scss" scoped>
</style>



