<template>
  <div>
    <Landing />
    <ProblemStatement />
    <Discovery />
    <UserInterview />
    <Persona />
    <Concepts />
    <UserFlows />
    <Prototyping />
    <Iterations />
    <UsabilityTesting />
    <HighFidelityWireframes />
    <FinalUi />
    <KeyLearnings />
    <NextProjectRouter
      :currentIdx="2"
    />
  </div>
</template>

<script>
import Landing from './Landing';
import ProblemStatement from './ProblemStatement';
import Discovery from './Discovery';
import UserInterview from './UserInterview';
import Persona from './Persona';
import Concepts from './Concepts';
import UserFlows from './UserFlows';
import Prototyping from './Prototyping';
import Iterations from './Iterations';
import UsabilityTesting from './UsabilityTesting';
import HighFidelityWireframes from './HighFidelityWireframes';
import FinalUi from './FinalUi';
import KeyLearnings from './KeyLearnings';
import NextProjectRouter from "../NextProjectRouter";


export default {
  name: 'GoCards',
  components: {
    Landing,
    ProblemStatement,
    Discovery,
    UserInterview,
    Persona,
    Concepts,
    UserFlows,
    Prototyping,
    Iterations,
    UsabilityTesting,
    HighFidelityWireframes,
    FinalUi,
    KeyLearnings,
    NextProjectRouter,
  },
  mounted () {
    this.$ga.page({
      page: '/gocards',
      title: 'Go Cards',
      location: window.location.href
    });
  },
};
</script>



