<template>
  <ParagraphWithCarousel
    title="User Flows"
    paragraph="I created a user flow for the key tasks within the app: onboarding and registration, create custom vocabulary study sets and create flashcards. By going through the flow of the key tasks it helped me think through the core activities and ensure a positive process from users."
    :carouselStyle="containerStyle"
    :carouselItems="items"
    :imgStyle="imgStyle"
  />
</template>

<script>
import ParagraphWithCarousel from '../common/ParagraphWithCarousel';

import gocard_userflow1 from "../../assets/img/gocard_userflow1.jpg";
import gocard_userflow2 from "../../assets/img/gocard_userflow2.jpg";

export default {
  components: {
    ParagraphWithCarousel,
  },
  data() {
    return {
      items: [
        {img: gocard_userflow1, alt: "User Flows"},
        {img: gocard_userflow2, alt: "User Flows"},
      ],
      containerStyle: {
        height: "100%"
      },
      imgStyle: {
        padding: "24px",
        width: "80%",
        margin: "0 auto",
      },
    };
  }

};
</script>

<style lang="scss" scoped>

</style>



