<template>
  <ParagraphWithCarousel
    title="Iterations"
    paragraph="After brainstorming, I made a few iterations to decrease the amount of clicks to get to the next step."
    :carouselStyle="containerStyle"
    :carouselItems="items"
    :imgStyle="imgStyle"
  />
</template>

<script>
import ParagraphWithCarousel from '../common/ParagraphWithCarousel';

import gocard_iteration_1 from "../../assets/img/gocard_iteration_1.jpg";
import gocard_iteration_2 from "../../assets/img/gocard_iteration_2.jpg";

export default {
  components: {
    ParagraphWithCarousel,
  },
  data() {
    return {
      items: [
        {img: gocard_iteration_1, alt: "Ideas for iteration"},
        {img: gocard_iteration_2, alt: "Ideas for iteration"},
      ],
      containerStyle: {
        height: "100%"
      },
      imgStyle: {
        padding: "24px",
        width: "80%",
        margin: "0 auto",
      },
    };
  }

};
</script>

<style lang="scss" scoped>

</style>



