<template>
  <Width100>
    <VueImage :src="gocards_cover_page" alt="Gocards cover page" />
  </Width100>
</template>

<script>
import VueImage from '../common/VueImage';
import Width100 from '../common/layout/Width100';

import gocards_cover_page from "../../assets/img/gocards_cover_page.png";

export default {
  components: {
    VueImage,
    Width100,
  },
  data() {
    return {
      gocards_cover_page
    }
  }
};
</script>



