<template>
  <div>
    <Width100>
      <Paragraph
        v-bind:center="true"
        title="Discovery"
        subtitle="Competitive Analysis"
        paragraph="I used competitive analysis to better understand the problem and to get me thinking about some solutions that have already been proposed to address the problem. I looked at 3 mobile apps that focus on learning new vocabulary: Magoosh, Quizlet Flashcard, and Memrise."
      />
    </Width100>

    <Width100>
      <Width50>
        <OrderedList
          title="Observations"
          titleColor="#6200EE"
          v-bind:items="observations"
        />
      </Width50>
      <Width50>
        <OrderedList
          title="Key Insights"
          titleColor="#6200EE"
          v-bind:items="keyInsights"
        />
      </Width50>
    </Width100>
    <LineBreak />
  </div>
</template>

<script>
import LineBreak from '../common/LineBreak';
import OrderedList from '../common/OrderedList';
import Paragraph from '../common/Paragraph';
import Width50 from '../common/layout/Width50';
import Width100 from '../common/layout/Width100';

export default {
  components: {
    LineBreak,
    OrderedList,
    Paragraph,
    Width50,
    Width100,
  },
  data() {
    return {
      observations: [
        "Success for people is due to perseverance and repetition.",
        "Difficult to learn something new if users have no interest in it.",
        "Studying is an on-going process and it takes time to figure things out.",
      ],
      keyInsights: [
        "Easier to learn with visuals because there are images to reference. Words and visuals have a great way of conveying a meaning across.",
        "Looks at mobile app like Duolingo and YouTube videos to learn.",
        "Users like to engage with visuals and audio and less reading.",
        "Users look for apps that are free and easy to use on the go.",
        "Absorbs more knowledge and less likely to get distracted when visuals are nice to look at.",
      ],
    };
  }

};
</script>

<style lang="scss" scoped>
</style>



