<template>
  <div>
    <Width100>
      <Paragraph
        v-bind:center="true"
        title="Discovery"
        subtitle="Competitive Analysis"
        paragraph="I used competitive analysis to better understand the problem and to get me thinking about some solutions that have already been proposed to address the problem. I looked at 3 mobile apps that focus on learning new vocabulary: Magoosh, Quizlet Flashcard, and Memrise."
      />
    </Width100>

    <Width100>
        <OrderedList
          title="Key Findings"
          titleColor="#6200EE"
          v-bind:items="observations"
        />
    </Width100>
    <LineBreak />
  </div>
</template>

<script>
import LineBreak from '../common/LineBreak';
import OrderedList from '../common/OrderedList';
import Paragraph from '../common/Paragraph';
import Width50 from '../common/layout/Width50';
import Width100 from '../common/layout/Width100';

export default {
  components: {
    LineBreak,
    OrderedList,
    Paragraph,
    Width50,
    Width100,
  },
  data() {
    return {
      observations: [
        "All apps focused a lot on the visuals and audio to enhance the learning.",
        "By incorporating learning games and rewards, it enables users to learn new words in an addicting and fun way by giving them a sense of accomplishment.",
        "The ability to pick your level lets the app not be too challenged or too easy which gives the app more sense of interest to the user and help them learn.",
        "Some apps allow users to build their vocabulary by reading and listening games.",
      ],
    };
  }

};
</script>

<style lang="scss" scoped>
</style>



