<template>
  <div>
    <Width100>
      <Paragraph
        v-bind:center="true"
        title="Usability Testing"
        subtitle="Is it useful and functional?"
        paragraph="Before perfecting the app to pixel-perfect, I wanted to make sure that users were able to use the navigation and use the basic functionality of the GoCard App."
      />
    </Width100>

    <Width100 v-bind:addPadding="true">
      <Width50>
        <OrderedList
          title="Goals & Methodologies"
          titleColor="#6200EE"
          v-bind:items="goals"
        />
      </Width50>
      <Width50>
        <OrderedList
          title="User Testing Tasks"
          titleColor="#6200EE"
          v-bind:items="testing"
        />
      </Width50>
    </Width100>
    <LineBreak />
  </div>
</template>

<script>
import LineBreak from '../common/LineBreak';
import OrderedList from '../common/OrderedList';
import Paragraph from '../common/Paragraph';
import Width50 from '../common/layout/Width50';
import Width100 from '../common/layout/Width100';

export default {
  components: {
    LineBreak,
    OrderedList,
    Paragraph,
    Width50,
    Width100,
  },
  data() {
    return {
      goals: [
        "The aim of the test is to take a closer look at the main functionalities of the app to determine its overall usefulness.",
        "Identify any problems in order to improve these elements prior further testing.",
        "I hope to understand my user's needs and gain new clarity regarding the navigation of the current app prototype.",
        "The usability tests will be conducted moderated in-peron. Participants will use my iPhone 8+",
      ],
      testing: [
        "Create new account.",
        "Add new study sets.",
        "Create new flashcards to study sets.",
        "Review and test new learnings.",
      ],
    };
  }

};
</script>

<style lang="scss" scoped>
</style>



