<template>
  <ParagraphWithCarousel
    title="High Fidelity Wireframes"
    paragraph="Integrating Visual Design Principles, Material Design, accessibility and feedback from fellow designers, I have repolished my mobile app."
    :carouselStyle="containerStyle"
    :carouselItems="items"
    :imgStyle="imgStyle"
  />
</template>

<script>
import ParagraphWithCarousel from '../common/ParagraphWithCarousel';

import gocard_highfidelity1_cover from "../../assets/img/gocard_highfidelity1_cover.jpg";
import gocard_finalui2 from "../../assets/img/gocard_finalui2.jpg";
import gocard_finalui3 from "../../assets/img/gocard_finalui3.jpg";
import gocard_finalui4 from "../../assets/img/gocard_finalui4.jpg";
import gocard_finalui5 from "../../assets/img/gocard_finalui5.jpg";
import gocard_finalui6 from "../../assets/img/gocard_finalui6.1.jpg";

export default {
  components: {
    ParagraphWithCarousel,
  },
  data() {
    return {
      items: [
        {img: gocard_highfidelity1_cover, alt: "High fidelity wireframes"},
        {img: gocard_finalui2, alt: "High fidelity wireframes"},
        {img: gocard_finalui3, alt: "High fidelity wireframes"},
        {img: gocard_finalui4, alt: "High fidelity wireframes"},
        {img: gocard_finalui5, alt: "High fidelity wireframes"},
        {img: gocard_finalui6, alt: "High fidelity wireframes"},
      ],
      containerStyle: {
        height: "100%"
      },
      imgStyle: {
        padding: "24px",
        width: "80%",
        margin: "0 auto",
      },
    };
  }

};
</script>

<style lang="scss" scoped>

</style>



