<template>
  <ParagraphWithCarousel
    title="Prototyping & Testing"
    subtitle="Wireframes"
    paragraph="A progression of key screens from low to high fidelity wireframes.These iterations were based on feedback received from user testing."
    :carouselStyle="containerStyle"
    :carouselItems="items"
    :imgStyle="imgStyle"
  />
</template>

<script>
import ParagraphWithCarousel from '../common/ParagraphWithCarousel';

import gocard_lowfidelity_1 from "../../assets/img/gocard_lowfidelity_1.jpg";
import gocard_lowfidelity_2 from "../../assets/img/gocard_lowfidelity_2.jpg";
import gocard_lowfidelity_3 from "../../assets/img/gocard_lowfidelity_3.1.jpg";
import gocard_lowfidelity_4 from "../../assets/img/gocard_lowfidelity_4.jpg";
import gocard_lowfidelity_5 from "../../assets/img/gocard_lowfidelity_5.jpg";
import gocard_lowfidelity_6 from "../../assets/img/gocard_lowfidelity_6.jpg";
import gocard_highfidelity_1 from "../../assets/img/gocard_highfidelity_1.jpg";
import gocard_highfidelity_2 from "../../assets/img/gocard_highfidelity_2.jpg";
import gocard_highfidelity_3 from "../../assets/img/gocard_highfidelity_3.1.jpg";
import gocard_highfidelity_4 from "../../assets/img/gocard_highfidelity_4.jpg";
import gocard_highfidelity_5 from "../../assets/img/gocard_highfidelity_5.jpg";
import gocard_highfidelity_6 from "../../assets/img/gocard_highfidelity_6.jpg";

export default {
  components: {
    ParagraphWithCarousel,
  },
  data() {
    return {
      items: [
        {img: gocard_lowfidelity_1, alt: "Low fidelity mockups"},
        {img: gocard_lowfidelity_2, alt: "Low fidelity mockups"},
        {img: gocard_lowfidelity_3, alt: "Low fidelity mockups"},
        {img: gocard_lowfidelity_4, alt: "Low fidelity mockups"},
        {img: gocard_lowfidelity_5, alt: "Low fidelity mockups"},
        {img: gocard_lowfidelity_6, alt: "Low fidelity mockups"},
        {img: gocard_highfidelity_1, alt: "High fidelity mockups"},
        {img: gocard_highfidelity_2, alt: "High fidelity mockups"},
        {img: gocard_highfidelity_3, alt: "High fidelity mockups"},
        {img: gocard_highfidelity_4, alt: "High fidelity mockups"},
        {img: gocard_highfidelity_5, alt: "High fidelity mockups"},
        {img: gocard_highfidelity_6, alt: "High fidelity mockups"},
      ],
      containerStyle: {
        height: "100%"
      },
      imgStyle: {
        padding: "24px",
        width: "80%",
        margin: "0 auto",
      },
    };
  }

};
</script>

